/* eslint-disable */
import React, { useEffect, useState } from 'react';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';         // styled-components
import "./Detail.css";



// 과거 LifeCycleHook
// class Detail2 extends React.Component {
//   // 컴포넌트가 마운트 될 때 실행
//   componentDidMount(){}   // ajax 같은 걸 여기다가 많이 사용함
//   // 컴포넌트가 언마운트(=사라질때) 될 때 실행
//   componentWillUnmount(){
//   }
// }

function Detail(props) {

  let { id }  = useParams();           // 사용자가 입력한 URL 파라미터들
  let history = useHistory();
  
  let Box     = styled.div`padding: 20px;`;   // style이 입혀진 div 생성
  let Subj    = styled.h4`font-size:25px;color:${ props=>props.color }`;
  let [subjShow, showSetter]  = useState(true);
  let [inpVal, valSetter]     = useState('');
  // useEffect 훅
  // 컴포넌트가 mount, update 되었을 때 특정 코드를 실행할 수 있음.
  // return 이 후에 정의한 함수에는 unmount 될 때 특정 코드를 실행할 수 있음.
  // useEffect(()=>{ return ()=> {console.log('Detail Die');} });
  // 마지막에 []를 넣으면 Update 시 실행하는 조건을 입력할 수 있다. -> useEffect의 조건식
  // useEffect는 여러개를 선언할 수 있고, 선언한 순서대로 실행된다.
  useEffect(()=>{
    let timer = setTimeout(() => { showSetter(false); }, 2000);
    return ()=>{clearTimeout(timer)}
  }, [subjShow]);

  let fontCr  = "blue";
  let product = props.shoes.find(function(prd){
      return prd.id == id
    });    
  
  return(
    <div className="container">
      <Box>
        <Subj className="red">Detail</Subj>
        { /*<Subj color={fontCr}>Detail</Subj>*/ }
      </Box>
      {inpVal}
      <input onInput={(e)=>{ valSetter(e.target.value) }} />
      { subjShow === true ? <div className="my-alert"><p>재고가 얼마 남지 않았습니다.</p></div> : null }
      <div className="row">
        <div className="col-md-6">
          <img src={ "https://codingapple1.github.io/shop/shoes" + (parseInt(id)+1) + ".jpg" } width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{ product.title }</h4>
          <p>{ product.content }</p>
          <p>{ product.price }원</p>
          <Info stock={props.stock}/>
          <button className="btn btn-danger" onClick={()=>{ props.stockSetter([9,11,12]) }}>주문하기</button>  <button className="btn btn-danger" onClick={ ()=>{ history.goBack(); } }>뒤로가기</button> 
        </div>
      </div>
    </div> 
  )
}

function Info(props) {
  return (
    <p>재고 : {props.stock[0]}</p>
  )
}

  export default Detail;